const test = require('ava')
const {decode} = require('ini')
const {random, times} = require('lodash/fp')
const matchAll = require('match-all')

const {getFileFromContainer} = require('./util')

const DEFAULT_CONFIG = {
  network: 'main',
}

const mergeConfig = config => ({
  ...DEFAULT_CONFIG,
  ...config,
})

const getOptions = async config => {
  const factomdConf = await getFileFromContainer('factomd.conf', mergeConfig(config))
  return decode(factomdConf).app
}

const getArgs = async config => {
  const startupScript = await getFileFromContainer('start.sh', mergeConfig(config))
  const regex = /-([a-z]+)=("?[^ \"]+"?)/g

  let match, args = {}
  while ((match = regex.exec(startupScript)) != null) {
    const [_, key, value] = match
    args[key] = value
  }
  return args
}

const generateHexId = () => times( () => random(0, 15).toString(16), 64).join('')


console.log(generateHexId())

const optionTest = (yamlName, optionName, config, valueOverride) => test(`${yamlName} -> ${optionName}`, async t => {
  const options = await getOptions(config)
  t.is(valueOverride || config[yamlName], options[optionName])
})

const argTest = (yamlName, argName, config, valueOverride) => test(`${yamlName} -> ${argName}`, async t => {
  const args = await getArgs(config)
  t.is(valueOverride || config[yamlName], args[argName])
})

optionTest('apiPassword', 'FactomdRpcPass', {apiPassword: 'password', apiUser: 'username'})
optionTest('apiPort', 'PortNumber', {apiPort: 1234}, '1234')
optionTest('apiUser', 'FactomdRpcUser', {apiPassword: 'password', apiUser: 'username'})
argTest('faultTimeoutInSeconds', 'faulttimeout', {faultTimeoutInSeconds: 123}, '123')
